html {
  box-sizing: border-box; }

*, *:before, *:after {
  box-sizing: inherit; }

html, body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', 'Helvetica', 'Arial', sans-serif; }

body {
  background: #eee; }

h1, h2, h3, h4, h5, h6 {
  font-family: 'Cairo'; }

h1 {
  font-size: 40px;
  font-weight: bold;
  margin-bottom: 30px;
  color: #222; }

h2 {
  font-size: 30px;
  font-weight: bold; }

h3 {
  font-size: 22px;
  font-weight: bold; }

p {
  line-height: 1.5;
  margin-bottom: 15px; }

#App {
  width: 100%;
  height: 100vh; }
